







import { Vue, Component, Prop } from 'vue-property-decorator'

@Component
export default class Badge extends Vue {
  @Prop() count!:number

  get formatCount() {
    if (this.count < 100) {
      return this.count
    } else if (this.count >= 100) {
      return '99+'
    } else {
      return 0
    }
  }
}
